header{
	order: 1;	
    -webkit-order: 1;
}
.header{
	background: #000;
	width: 100px;
	height: 100%;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 100;
	&.flex{
		flex-direction: column;
		justify-content: space-between;
		align-items: stretch;
	}
	&__logo{
		width: 100%;
		img{
			width: 100%;
		}
	}
	&__nav{
		&.flex{
			flex-direction: column;
			justify-content: flex-start;
			align-items: stretch;
		}
	}
	&__navitem{
		padding: 18px 0 20px;
		border-bottom: 1px solid #fff;
		border-left: 1px solid #fff;
		cursor: pointer;
		transition: all 0.3s ease;
		&.flex{
			flex-direction: column;
			justify-content: center;
		}
		i{
			font-size: 25px;
			color: #fff;
			transition: all 0.3s ease;
		}
		span{
			font: 700 15px $font;
			color: #fff;
			display: inline-block;
			margin-top: 5px;
			transition: all 0.3s ease;
		}
		&:hover, &.active{
			border-left: 1px solid #FFC300;
			i{
				animation: socicons 0.3s ease;
				color: #FFC300;
			}
			span{
				transform: scale(0.9);
				color: #FFC300;
			}
		}
	}
	&__soc{
		padding-bottom: 15px;
		&.flex{
			justify-content: center;
		}
		a{
			font-size: 18px;
			color: #fff;
			display: inline-block;
			margin: 0 5px;
			&:hover{
				color: #FFC300;
				animation: socicons 0.3s ease;
			}
		}
	}
}
@keyframes socicons{
	0%{
		transform: rotate(0deg);
	}
	25%{
		transform: rotate(-10deg);
	}
	50%{
		transform: rotate(0deg);
	}
	75%{
		transform: rotate(10deg);
	}
	100%{
		transform: rotate(0deg);
	}
}
.hamb{
	background: #222;
	position: fixed;
	top: 0px;
	right: 0px;
	display: none;
	padding: 10px 10px;
	z-index: 100;
	&__toggle{
		width: 40px;
		height: 26px;
		position: relative;
		transition: .5s ease-in-out;
		cursor: pointer;
		span{
			display: block;
			position: absolute;
			height: 4px;
			width: 100%;
			background: #fff;
			border-radius: 2px;
			opacity: 1;
			left: 0;
			transform: rotate(0deg);
			transition: .25s ease-in-out;
			&:nth-child(1){
				top: 0px;
			}
			&:nth-child(2){
				top: 11px;
			}
			&:nth-child(3){
				top: 22px;
			}
		}
		&.open{
			span{
				&:nth-child(1){
					top: 11px;
					transform: rotate(135deg);
				}
				&:nth-child(2){
					opacity: 0;
					left: -40px;
				}
				&:nth-child(3){
					top: 11px;
					transform: rotate(-135deg);
				}
			}
		}
	}
}


