@media screen and (max-width: 767px) {
	.header{
		width: 90px;
		transform: translateX(-100%);
		transition: all 0.3s ease;
		&__navitem{
			padding: 10px 0;
		}
		&.active{
			transform: translateX(0%);
		}
	}
	.hamb{
		display: block;
	}
}
