.content{	
	order: 2;
    -webkit-order: 2;
    padding-left: 100px;
}
.cd-words-wrapper {
  	display: inline-block;
  	position: relative;
  	text-align: left;
}
.cd-words-wrapper b {
  	display: inline-block;
  	position: absolute;
  	white-space: nowrap;
  	left: 0;
  	top: 0;
}
.cd-words-wrapper b.is-visible {
  	position: relative;
}
.no-js .cd-words-wrapper b {
  	opacity: 0;
}
.no-js .cd-words-wrapper b.is-visible {
  	opacity: 1;
}
/* -------------------------------- 

xclip 

-------------------------------- */
.cd-headline.clip span {
  display: inline-block;
  padding: .2em 0;
}
.cd-headline.clip .cd-words-wrapper {
  overflow: hidden;
  vertical-align: top;
}
.cd-headline.clip .cd-words-wrapper::after {
  /* line */
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 2px;
  height: 100%;
  background-color: #aebcb9;
}
.cd-headline.clip b {
  opacity: 0;
}
.cd-headline.clip b.is-visible {
  opacity: 1;
}
.flex{
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
}
.btn{
	display: inline-block;
    background-color: transparent;
    padding: 10px 20px;
    border: 2px solid #FFC300;
    position: relative;
    transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    &:before{
    	content: "";
	    position: absolute;
	    top: 0;
	    left: 0;
	    bottom: 0;
	    right: 0;
	    background: #FFC300;
	    transform-origin: right center;
	    transform: scale(0, 1);
	    transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    }
    span{
    	color: #222;
    	font: 600 14px $font;
    	position: relative;
    	z-index: 2;
    }
    &:hover{
    	&:before{
    		transform-origin: left center;
    		transform: scale(1, 1);
    	}
    	span{
    		color: #fff;
    	}
    }
}
.title{
	text-align: center;
	margin-bottom: 30px;
	h5{
		color: #222;
		font: 500 16px $font;
		text-transform: uppercase;
		margin-bottom: 10px;
		display: inline-block;
		position: relative;
	}
	h2{
		color: #222;
		font: 600 30px $font;
		text-transform: uppercase;
	}
	&_2{
		text-align: left;
		h5{
			margin-bottom: 0;
			&:after{
				content: "";
				width: 50px;
				height: 2px;
				background: #FFC300;
				position: absolute;
				right: -60px;
				top: 50%;
				margin-top: -1px;
			}
		}
		h2{
			margin-bottom: 10px;
		}
	}
}
.sound{
	position: fixed;
	width: 25px;
	height: 25px;
	z-index: 100;
	top: 10%;
	right: 0;
	background: #222;
	cursor: pointer;
	&.flex{
		justify-content: center;
	}
	i{
		color: #fff;
		font-size: 14px;
		display: none;
		&.active{
			display: block;
		}
	}
}
.banner{
	width: 100%;
	min-height: 100vh;
	position: relative;
	/*padding-left: 100px;*/
	background: url(../img/logo.jpg) center center no-repeat;
	background-size: cover;
	box-shadow: 0 0 0 9999px rgba(0,0,0,0.6) inset;
	&__cont{
		width: 100%;
		height: 100vh;
		&.flex{
			justify-content: center;
		}
	}
	&__box{
		&.flex{
			flex-direction: column;
			justify-content: center;
		}
		h5{
			font: 500 40px $font;
			color: #fff;
		}
		h1{
			font: 600 80px $font;
			color: #fff;
		}
		h2{
			span{
				b{
					font: 700 20px $font;
					color: #fff;
					i{
						color: #FFC300;
						font-style: normal;
					}
				}
			}
		}
	}
}
#particles-js {
  	position: absolute;
  	width: 100%;
  	height: 100%;
  	background-color: none;
  	background-image: none;
  	background-repeat: no-repeat;
  	background-size: cover;
  	background-position: 50% 50%;
}
.about{
	background: #fff;
	width: 100%;
	min-height: 100vh;
	/*padding-left: 100px;*/
	&__cont{
		width: 1140px;
		max-width: 90%;
		margin: 0 auto;
		&.flex{
			justify-content: center;
			align-items: flex-start;
		}
	}
	&__img{
		width: 400px;
		position: relative;
		margin-right: 50px;
		img{
			width: 100%;
			position: relative;
			z-index: 2;
		}
		&:before{
			content: "";
		    background-color: #f2f2f2;
		    width: 100%;
		    height: 100%;
		    position: absolute;
		    bottom: -10%;
		    left: -10%;
		    z-index: 1;
		    transition: all 0.3s ease;
		}
		&:after{
			content: "";
		    background-color: #FFC300;
		    opacity: 0.5;
		    width: 100%;
		    height: 100%;
		    position: absolute;
		    bottom: -10%;
		    left: -10%;
		    z-index: 0;
		    transition: all 0.3s ease;
		}
		&:hover{
			&:before{
				opacity: 0;
				bottom: -5%;
		    	left: -5%;
			}
			&:after{
				bottom: -5%;
		    	left: -5%;
			}
		}
	}
	&__info{
		max-width: 650px;
		width: calc(100% - 450px);
		padding-top: 10px;
	}
	&__text{
		margin-bottom: 25px;
		p{
			font: 400 16px/25px $font;
			color: #222;
			margin-bottom: 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}
.portf{
	background: #f2f2f2;
	width: 100%;
	min-height: 100vh;
	/*padding-left: 100px;*/
	&__cont{
		width: 1140px;
		max-width: 95%;
		margin: 0 auto;
	}
	&__item{
		background: none;
		width: 270px;
		max-width: 23%;
		margin-bottom: 25px;
		position: relative;
		cursor: pointer;
		img{
			max-width: 100%;
		}
		&:hover{
			.portf{
				&__overlay{
					transform: scale(1);
				}
			}
		}
	}
	&__overlay{
		position: absolute;
		left: 5%;
		top: 5%;
		width: 90%;
		height: 90%;
		border-radius: 7px;
		box-shadow: 0 0 0 999px rgba(255,195,0, 0.9) inset;
		transform: scale(0);
		transition: all 0.3s ease-in-out;
		&.flex{
			flex-direction: column;
			justify-content: center;
		}
		h3{
			font: 600 20px $font;
			color: #fff;
			margin-bottom: 20px;
		}
		i{
			color: #fff;
			font-size: 20px;
		}
	}
}
.feedback{
	width: 100%;
	padding: 50px 0 70px;
	background: url(../img/banner.jpg) center center no-repeat;
	background-size: cover;
	box-shadow: 0 0 0 9999px rgba(0,0,0,0.3) inset;
	&__slider{
		width: 1140px;
		max-width: 95%;
		margin: 0 auto;
		.slick-dots{
			list-style: none;
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			left: 0;
			bottom: -35px;
			li{
				padding: 0;
				margin: 0 5px;
				button{
					padding: 0;
					background: #fff;
					border: 0;
					text-indent: 9999px;
					width: 15px;
					height: 2px;
				}
				&.slick-active{
					button{
						background: #FFC300;
					}
				}
			}
		}
	}
	&__item{
		width: 100%;
		text-align: center;
		h5{
			font: 500 16px $font;
			color: #fff;
			margin-bottom: 15px;
		}
		p{
			font: 400 20px/28px $font;
			color: #fff;
			margin-bottom: 20px;
		}
	}
	&__img{
		width: 80px;
		height: 80px;
		border-radius: 100%;
		margin: 0 auto;
		&_1{
			background: url(../img/user.jpg) center center no-repeat;
			background-size: cover;
		}
	}
}
.comment{
	background: url(../img/banner1.jpg) center center no-repeat;
	background-size: cover;
	box-shadow: 0 0 0 999px rgba(0, 0, 0, 0.6) inset;
	width: 100%;
	min-height: 100vh;
	/*padding-left: 100px;*/
	&__cont{
		width: 1140px;
		max-width: 95%;
		margin: 0 auto;
		.title{
			h5,h2{
				color: #fff;
			}
		}
	}
	&__form{
		width: 40%;
		margin-left: auto;
		margin-right: 0;
		input{
			width: 100%;
			height: 50px;
			border: 0;
			border-bottom: 1px solid #fff;
			background: none;
			font: 400 15px $font;
			color: #fff;
			margin-bottom: 15px;
			transition: all 0.3s ease;
			&::placeholder{
				font: 400 15px $font;
				color: #fff;
			}
			&:focus{
				padding: 0 15px;
				border-color: #FFC300;
			}
		}
		textarea{
			width: 100%;
			height: 150px;
			border: 0;
			border-bottom: 1px solid #fff;
			background: none;
			font: 400 15px $font;
			color: #fff;
			margin-bottom: 20px;
			transition: all 0.3s ease;
			padding: 10px 0;
			&::placeholder{
				font: 400 15px $font;
				color: #fff;
			}
			&:focus{
				padding: 10px 15px;
				border-color: #FFC300;
			}
		}
		.btn{
			span{
				color: #fff;
			}
		}
	}
}
.contact{
	background: #f2f2f2;
	width: 100%;
	min-height: 100vh;
	/*padding-left: 100px;*/
	&__cont{
		width: 1140px;
		max-width: 95%;
		margin: 0 auto;
	}
	&__form{
		margin-left: 0;
		margin-right: auto;
		input{
			color: #222;
			border-color: #222;
			&::placeholder{
				color: #222;
			}
		}
		textarea{
			color: #222;
			border-color: #222;
			&::placeholder{
				color: #222;
			}
		}
		.btn{
			span{
				color: #222;
			}
		}
	}
	&__info{
		width: 50%;
		&.flex{
			justify-content: space-around;
		}
	}
	&__item{
		width: 220px;
		padding: 35px 25px;
		transition: all 0.3s ease-in-out;
		margin: 15px 0;
		&.flex{
			flex-direction: column;
			justify-content: center;
		}
		i{
			font-size: 35px;
			color: #222;
		}
		span, a{
			font: 500 18px $font;
			color: #222;
			display: inline-block;
			margin-top: 15px;
		}
		&:hover{
			box-shadow: 0px 5px 15px 1px rgba(0,0,0,0.2);
		}
	}
}



@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    body{min-height: 100hv; width: 100%; display: flex; flex-direction: column;}
}
@media screen and (min-width: 992px) and (max-width: 1200px) {
	.about{
		padding: 40px 0;
		&__cont{
			&.flex{
				justify-content: center;
			}
		}
		&__img{
			margin-right: 0;
			margin-bottom: 70px;
		}
		&__info{
			max-width: 100%;
			width: 100%;
		}
	}
	.portf{
		min-height: auto;
    	padding: 50px 0;
    	&__overlay{
    		display: none;
    	}
	}
	.comment{
		min-height: auto;
    	padding: 50px 0;
	}
	.contact{
		min-height: auto;
    	padding: 50px 0;
    	&__item{
    		width: 210px;
    		span, a{
    			font-size: 16px;
    		}
    	}
	}
}

