    	}
	}
	.contact{
		min-height: auto;
    	padding: 50px 0 20px;
    	&__form{
    		width: 90%;
    		margin-left: auto;
    		margin-bottom: 20px;
    	}
    	&__item{
    		width: 210px;
    		span, a{
    			font-size: 16px;
    		}
    	}
    	&__info{
    		width: 600px;
    		margin: 0 auto;
    	}
	}
}

