footer{
	order: 3;	
    -webkit-order: 3;	
}
.footer{
	padding: 15px 0;
	padding-left: 100px;
	&__cont{
		width: 1140px;
		max-width: 95%;
		margin: 0 auto;
	}
	&__text{
		font: 500 14px $font;
		color: #222;
		a{
			font: 500 14px $font;
			color: #222;
			&:hover{
				color: #FFC300;
			}
		}
	}
}


@media screen and (min-width: 992px) and (max-width: 1200px) {
	
}

@media screen and (min-width: 768px) and (max-width: 991px) {
	
}

@media screen and (max-width: 767px) {
	.footer{
		padding-left: 0;
		&__text{
			margin: 0 auto;
		}
	}
}